'use client';

// AppState - Central State Machine for fllc.net
// SINGLETON: Single source of truth for application phases
// IMPORTANT: Import from this exact path only to ensure single instance

export const AppPhase = {
  GATE: 'GATE',           // Disclaimer/terms (first visit only)
  BOOT: 'BOOT',           // Loading/boot sequence
  DESKTOP: 'DESKTOP',     // Desktop with icons
  APP_OPEN: 'APP_OPEN',   // Window manager active
  GAME: 'GAME'            // CyberWorld running
} as const;

export type AppPhaseType = typeof AppPhase[keyof typeof AppPhase];

const DISCLAIMER_VERSION = 'v1';
const STORAGE_KEY = 'fllc_net_state';

interface AppStateData {
  disclaimerAccepted: string | null; // Version string or null
  bootSeen: boolean;
  lastPhase: AppPhaseType | null;
  phase: AppPhaseType;
}

interface AppStateSnapshot {
  phase: AppPhaseType;
  disclaimerAccepted: string | null;
  bootSeen: boolean;
  lastTransitionTime: number;
}

class AppStateManager {
  private phase: AppPhaseType = AppPhase.GATE;
  private disclaimerAccepted: string | null = null;
  private bootSeen: boolean = false;
  private lastPhase: AppPhaseType | null = null;
  private listeners: Set<() => void> = new Set();
  private initialized: boolean = false;
  private lastTransitionTime: number = 0;
  private cachedSnapshot: AppStateSnapshot | null = null;

  constructor() {
    // Only initialize once, and only on client
    if (typeof window !== 'undefined' && !this.initialized) {
      this.initialized = true;
      this.loadFromStorage();
      console.log('[AppState] Singleton initialized, initial phase:', this.phase);
      console.log('[AppState] Disclaimer accepted:', this.disclaimerAccepted === DISCLAIMER_VERSION);
      console.log('[AppState] Boot seen:', this.bootSeen);
    }
  }

  private loadFromStorage() {
    try {
      // SKIP LOADING FROM STORAGE - Always go directly to Desktop
      // User wants to skip disclaimer and boot sequence
      this.disclaimerAccepted = DISCLAIMER_VERSION;
      this.bootSeen = true;
      this.phase = AppPhase.DESKTOP;
      this.cachedSnapshot = null;
      
      console.log('[AppState] Skipping disclaimer/boot - going directly to Desktop');
    } catch (error) {
      console.error('[AppState] Error loading state:', error);
      // Default to Desktop on error
      this.disclaimerAccepted = DISCLAIMER_VERSION;
      this.bootSeen = true;
      this.phase = AppPhase.DESKTOP;
      this.cachedSnapshot = null;
    }
  }

  private saveToStorage() {
    try {
      const data: AppStateData = {
        disclaimerAccepted: this.disclaimerAccepted,
        bootSeen: this.bootSeen,
        lastPhase: this.lastPhase,
        phase: this.phase
      };
      localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
    } catch (error) {
      console.error('Error saving state:', error);
    }
  }

  setPhase(newPhase: AppPhaseType) {
    if (this.phase === newPhase) {
      console.log(`[AppState] Phase unchanged: ${newPhase}`);
      return;
    }
    
    const oldPhase = this.phase;
    this.lastPhase = oldPhase;
    this.phase = newPhase;
    this.lastTransitionTime = Date.now();
    
    // Invalidate cached snapshot
    this.cachedSnapshot = null;
    
    console.log(`[AppState] Phase transition: ${oldPhase} â†’ ${newPhase} (at ${new Date().toISOString()})`);
    
    // Update persistence
    if (newPhase === AppPhase.DESKTOP) {
      this.bootSeen = true;
      console.log('[AppState] Marking boot as seen');
    }
    
    this.saveToStorage();
    this.notifyListeners(oldPhase, newPhase);
  }
  
  getLastTransitionTime(): number {
    return this.lastTransitionTime;
  }
  
  getDisclaimerVersion(): string | null {
    return this.disclaimerAccepted;
  }

  acceptDisclaimer() {
    console.log('[AppState] acceptDisclaimer() called');
    this.disclaimerAccepted = DISCLAIMER_VERSION;
    this.cachedSnapshot = null; // Invalidate cache
    this.saveToStorage();
    console.log('[AppState] Disclaimer accepted, transitioning to BOOT');
    this.setPhase(AppPhase.BOOT);
  }

  completeBoot() {
    console.log('[AppState] completeBoot() called');
    this.bootSeen = true;
    this.cachedSnapshot = null; // Invalidate cache
    this.saveToStorage();
    console.log('[AppState] Boot complete, transitioning to DESKTOP');
    this.setPhase(AppPhase.DESKTOP);
  }

  openApp(appId: string) {
    this.setPhase(AppPhase.APP_OPEN);
    // App-specific state can be stored here
    (this as any).currentApp = appId;
  }

  startGame() {
    this.setPhase(AppPhase.GAME);
  }

  closeApp() {
    this.setPhase(AppPhase.DESKTOP);
    (this as any).currentApp = null;
  }

  subscribe(listener: () => void) {
    this.listeners.add(listener);
    return () => {
      this.listeners.delete(listener);
    };
  }

  private notifyListeners(oldPhase: AppPhaseType, newPhase: AppPhaseType) {
    // Notify all listeners (for useSyncExternalStore compatibility)
    this.listeners.forEach(listener => {
      try {
        listener();
      } catch (error) {
        console.error('Error in state listener:', error);
      }
    });
  }

  getSnapshot(): AppStateSnapshot {
    // Safe default for SSR
    if (typeof window === 'undefined') {
      return {
        phase: AppPhase.GATE,
        disclaimerAccepted: null,
        bootSeen: false,
        lastTransitionTime: 0,
      };
    }
    
    // Return cached snapshot if state hasn't changed
    if (this.cachedSnapshot) {
      return this.cachedSnapshot;
    }
    
    // Create new snapshot and cache it
    this.cachedSnapshot = {
      phase: this.phase,
      disclaimerAccepted: this.disclaimerAccepted,
      bootSeen: this.bootSeen,
      lastTransitionTime: this.lastTransitionTime,
    };
    
    return this.cachedSnapshot;
  }

  getPhase(): AppPhaseType {
    return this.phase;
  }

  isDisclaimerAccepted(): boolean {
    return this.disclaimerAccepted === DISCLAIMER_VERSION;
  }

  hasBootSeen(): boolean {
    return this.bootSeen;
  }
}

// Export singleton - safe for SSR
let appStateInstance: AppStateManager | null = null;

export const appState = (() => {
  if (typeof window === 'undefined') {
    // Server-side: create instance but it won't access localStorage
    return new AppStateManager();
  }
  // Client-side: create singleton
  if (!appStateInstance) {
    appStateInstance = new AppStateManager();
  }
  return appStateInstance;
})();
