'use client';

import { useSyncExternalStore, useEffect } from 'react';
import { appState, AppPhase } from '@/lib/state-machine';

// Safe server snapshot
const getServerSnapshot = () => ({
  phase: AppPhase.GATE,
  disclaimerAccepted: null,
  bootSeen: false,
  lastTransitionTime: 0,
});

export function useAppState() {
  // Initialize state machine on client
  useEffect(() => {
    if (typeof window !== 'undefined') {
      // Ensure state machine is initialized
      appState.getSnapshot();
    }
  }, []);

  const snapshot = useSyncExternalStore(
    (cb) => {
      if (typeof window === 'undefined') {
        return () => {}; // No-op on server
      }
      try {
        return appState.subscribe(cb);
      } catch (error) {
        console.error('[useAppState] Subscribe error:', error);
        return () => {}; // Return no-op on error
      }
    },
    () => {
      if (typeof window === 'undefined') {
        return getServerSnapshot();
      }
      try {
        return appState.getSnapshot();
      } catch (error) {
        console.error('[useAppState] GetSnapshot error:', error);
        return getServerSnapshot();
      }
    },
    getServerSnapshot
  );

  return {
    ...snapshot,
    acceptDisclaimer: () => {
      console.log('[useAppState] acceptDisclaimer() called');
      appState.acceptDisclaimer();
    },
    completeBoot: () => {
      console.log('[useAppState] completeBoot() called');
      appState.completeBoot();
    },
    openApp: (appId: string) => appState.openApp(appId),
    startGame: () => appState.startGame(),
    closeApp: () => appState.closeApp(),
    isDisclaimerAccepted: () => appState.isDisclaimerAccepted(),
    hasBootSeen: () => appState.hasBootSeen(),
  };
}
